import React from 'react';
import {
  TextField,
  Typography,
  Button,
  Box,
  Grid,
  Paper
} from '@mui/material';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';

const validationSchema = Yup.object().shape({
  firstName: Yup.string()
    .min(2, '     2 ')
    .required(' '),
  lastName: Yup.string()
    .min(2, '     2 ')
    .required(' '),
  idNumber: Yup.string()
    .matches(/^\d{9}$/, '     9 ')
    .test('valid-israeli-id', '    ', (value) => {
      if (!value) return false;
      // Israeli ID validation algorithm
      const id = String(value).trim();
      if (id.length !== 9 || isNaN(id)) return false;
      
      const sum = id
        .split('')
        .map((num, idx) => {
          const digit = Number(num);
          let calc = digit * ((idx % 2) + 1);
          calc = calc > 9 ? calc - 9 : calc;
          return calc;
        })
        .reduce((acc, curr) => acc + curr, 0);
      
      return sum % 10 === 0;
    })
    .required(' '),
  birthDate: Yup.date()
    .max(new Date(), '    ')
    .test('minimum-age', '   18', function(value) {
      if (!value) return false;
      const cutoff = new Date();
      cutoff.setFullYear(cutoff.getFullYear() - 18);
      return value <= cutoff;
    })
    .required(' '),
  address: Yup.string()
    .min(5, '    5 ')
    .required(' '),
  phone: Yup.string()
    .matches(/^05\d{8}$/, '   ')
    .required(' '),
  email: Yup.string()
    .email('   ')
    .required(' ')
});

const PersonalDetailsStep = ({ onSubmit, data = {}, setFormData, error }) => {
  const handleFieldChange = (field, value) => {
    setFormData(prevData => ({
      ...prevData,
      personalDetails: {
        ...prevData.personalDetails,
        [field]: value?.trim ? value.trim() : value
      }
    }));
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, alignItems: 'center' }}>
      <Typography 
        variant="h5" 
        gutterBottom 
        align="center"
        sx={{
          fontSize: { xs: '1.2rem', sm: '1.5rem' },
          mb: { xs: 2, sm: 3 }
        }}
      >
                
      </Typography>
      
      <Grid container spacing={{ xs: 2, sm: 3 }} sx={{ mb: { xs: 2, sm: 3 } }}>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            required
            name="firstName"
            label=" "
            value={data?.personalDetails?.firstName || ''}
            onChange={(e) => handleFieldChange('firstName', e.target.value)}
            error={Boolean(error)}
            helperText={error}
            sx={{
              '& .MuiInputBase-root': {
                height: { xs: '45px', sm: '56px' }
              }
            }}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            required
            name="lastName"
            label=" "
            value={data?.personalDetails?.lastName || ''}
            onChange={(e) => handleFieldChange('lastName', e.target.value)}
            error={Boolean(error)}
            helperText={error}
            sx={{
              '& .MuiInputBase-root': {
                height: { xs: '45px', sm: '56px' }
              }
            }}
          />
        </Grid>
        
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            required
            name="idNumber"
            label=" "
            value={data?.personalDetails?.idNumber || ''}
            onChange={(e) => handleFieldChange('idNumber', e.target.value)}
            error={Boolean(error)}
            helperText={error}
            sx={{
              '& .MuiInputBase-root': {
                height: { xs: '45px', sm: '56px' }
              }
            }}
          />
        </Grid>
        
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            required
            type="date"
            name="birthDate"
            label=" "
            value={data?.personalDetails?.birthDate || ''}
            onChange={(e) => handleFieldChange('birthDate', e.target.value)}
            error={Boolean(error)}
            helperText={error}
            InputLabelProps={{ shrink: true }}
            sx={{
              '& .MuiInputBase-root': {
                height: { xs: '45px', sm: '56px' }
              }
            }}
          />
        </Grid>
        
        <Grid item xs={12}>
          <TextField
            fullWidth
            required
            name="address"
            label=""
            value={data?.personalDetails?.address || ''}
            onChange={(e) => handleFieldChange('address', e.target.value)}
            error={Boolean(error)}
            helperText={error}
            sx={{
              '& .MuiInputBase-root': {
                height: { xs: '45px', sm: '56px' }
              }
            }}
          />
        </Grid>
        
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            required
            name="phone"
            label=" "
            value={data?.personalDetails?.phone || ''}
            onChange={(e) => handleFieldChange('phone', e.target.value)}
            error={Boolean(error)}
            helperText={error}
            sx={{
              '& .MuiInputBase-root': {
                height: { xs: '45px', sm: '56px' }
              }
            }}
          />
        </Grid>
        
        <Grid item xs={12} sm={6}>
          <TextField
            fullWidth
            required
            name="email"
            label=""
            value={data?.personalDetails?.email || ''}
            onChange={(e) => handleFieldChange('email', e.target.value)}
            error={Boolean(error)}
            helperText={error}
            sx={{
              '& .MuiInputBase-root': {
                height: { xs: '45px', sm: '56px' }
              }
            }}
          />
        </Grid>
      </Grid>
    </Box>
  );
};

export default PersonalDetailsStep;
